<template lang="html">
  <div>
    <ul>
      <note v-for="(note, index) in notes" :key="note" v-bind:description="note.description" v-on:remove="removeNote(index)"></note>
      <form class="" action="index.html" method="post" v-on:submit.prevent="addNote()">
        <input type="text" name="" value="" v-model="newNote">
        <button type="submit" name="button" class="btn">Add Note</button>
      </form>
    </ul>
  </div>
</template>

<script>
import Note from './Note.vue'

export default {
  name: 'notes-list',
  components: {
    Note
  },
  data(){
    return {
      newNote: "",
      notes: []
    }
  },

  methods: {
    addNote(){
      if(this.newNote.length == 0){
        return;
      }
      this.notes.push({ description: this.newNote})
      this.newNote = "";
    },

    removeNote(index){
      this.notes.splice(index, 1);
    }
  }
}
</script>

<style scoped>
</style>



